import React, { Component } from 'react'
import { FcLike } from "react-icons/fc";
import "./likes.css"

class Likes extends Component {

    constructor(props) {
        super(props);

        this.state = {
            count: 0
        }};

    ChangeCount = () => {
        this.setState({
            count: this.state.count + 1

        })
    }

    render() {
        return (
            <div className = "like-box">
                
                <p className = "like-count">{this.state.count} </p>
                <FcLike  className = "like-icon" onClick = {this.ChangeCount}/>
                
            </div>
        )
    }
}

export default Likes
