

import React, { Component } from 'react'
import "./message.css"

class Message extends Component {
    // add constuctor
    constructor(props) {
        super(props);
        this.state = {
            message: "My name is Victor"
        }};
    
    DisplayName = () => {
        this.setState({
            message: "My name is Victor"
        });
    };

    DisplayJob = () => {
        this.setState({
            message: "I am a front end developer"
        });
    };

   

    render() {
        return (
            <div className = "message-box">

                
                <h1> {this.state.message}</h1>
                <div className = "button-wrapper"> 

                <button className = "names" onClick = {this.DisplayName}>Name</button>
                <button  className = "skills" onClick = {this.DisplayJob}>Skills</button>
                

                </div>
               
            </div>
        )
    }
}

export default Message

